.doctors__item a {
  position: relative;
  display: inline-block;
  vertical-align: baseline;
  overflow: hidden;
}

.doctors__item a:after {
  content: '';
  position: absolute;
  top: 95%;
  left: -100%;
  width: calc(100% - .7em);
  height: 2px;
  background-color: #41ead4;
  transition-duration: .2s;
