.accordeon {
  list-style-type: none;
  width: 100%;
  margin: 0;
  padding: 0;
}

.accordeon-tab {
  position: relative;
  cursor: pointer;
  z-index: 1;
}

.accordeon-tab:not(:first-of-type) {
  margin-top: -1px;
}

.accordeon-tab:hover {
  z-index: 2;
}

.accordeon-tab:hover .accordeon-tab__header {
  border-color: #9df4e9;
  background-color: #cbf9f3;
}

.accordeon-tab:hover .accordeon-tab__header:after {
  color: white;
  background-color: #41EAD4;
}

.accordeon-tab.is-active .accordeon-tab__header {
  border-color: #9df4e9;
  background-color: #b4f7ee;
}

.accordeon-tab.is-active .accordeon-tab__header:after {
  content: '-';
  color: white;
  background-color: #41EAD4;
}

.accordeon-tab.is-active .accordeon-tab__content {
  max-height: 2000px;
}

.accordeon-tab__header {
  position: relative;
  display: block;
  border: 1px solid #b4f7ee;
  background-color: #e2fcf8;
  padding: 8px;
  padding-right: calc(2em);
}

@media (min-width: 768px) {
  .accordeon-tab__header {
    padding-left: 18px;
    padding-right: 18px;
  }
}

.accordeon-tab__header:after {
  content: '+';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  color: white;
  background-color: #9df4e9;
  line-height: 1.2em;
  font-weight: 700;
  width: 1.2em;
  height: 1.2em;
  text-align: center;
}

@media (min-width: 768px) {
  .accordeon-tab__header:after {
    right: 18px;
  }
}

.accordeon-tab__content {
  overflow: hidden;
  max-height: 0;
  transition: max-height .5s;
}

.accordeon-tab__content-inner {
  padding-top: 18px;
  padding-bottom: 18px;
}

